import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
  state:{
    firstName: 'Alcatraz',
    counters: 0,
    defaultQuote: 'bu artik insanlarin kazanabilecegi bir savas degil. Artik Gelecekteyiz'
  },
  mutations:{
    increment(state, num){
      state.counters += num;
    },
    deincrement(state, num){
      state.counters -= num;
    }
  },
  actions:{

  },
  getters:{
    getQuote: state => {
      return state.defaultQuote
    }
  }
})



